* {
  padding: 0;
  border: 0;
}

body {
  background: rgb(255,0,0);
background: radial-gradient(circle, rgba(255,0,0,1) 0%, rgba(0,0,0,1) 73%);
  
  color: #17223b;
  text-align: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.container {
  box-sizing: border-box;
  max-width: 1040px;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
}
.row {
  display: flex;
}

input {
  border: none;
  border-radius: 0.4rem;
  width: 100%;
  padding: 10px 6px;
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  background-color: rgba(255, 0, 0, 0.79);
  color: black;
}
input:focus {
  outline: none;
}
.card {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
.card-container {
  margin: 20px 5px;
  border-radius: 20px;
  position: absolute;
  width: 470px;
  height: 470px;
  background-color: #000000;
  box-shadow: 0px 0px 30px 1px rgba(255, 0, 0, 0.382);
}
.city-name {
  font-weight: 300;
  font-size: 40px;
  color: #ffffff;
  bottom: 0;
}
.icon {
  position: relative;
  width: 300px;
  height: 300px;
  bottom: 280px;
  float: right;
  margin: 5px 5px 0 0;
}
.temp {
  position: relative;
  font-weight: 300;
  font-size: 80px;
  color: #ffffff;
  right: 120px;
}
.card-footer {
  background-color: rgb(255, 0, 0);
  color: aliceblue;
  position: relative;
  bottom: 280px;
  /* left: 0px; */
  border-radius: 0px 0px 20px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.temp-max-min {
  font-weight: 300;
  font-size: 50px;
  color: rgb(0, 0, 0);
}
.title {
  color: #ffffff;
}
